import jockey_statistics from "./data"
import React from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';



const JockeyStatisticsGraph = () => {
  return (
    <ResponsiveContainer width="100%" height="100%">
      <LineChart
        width={500}
        height={300}
        data={jockey_statistics}
        margin={{
          top: 5,
          right: 30,
          left: 20,
          bottom: 5,
        }}
      >
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="race_date" />
        <YAxis />
        <Tooltip />
        <Legend />
        <Line type="monotone" dataKey="W" stroke="red" activeDot={{ r: 8 }} />
        <Line type="monotone" dataKey="Q" stroke="blue" activeDot={{ r: 8 }} />
        <Line type="monotone" dataKey="P" stroke="green" activeDot={{ r: 8 }} />
        <Line type="monotone" dataKey="4" stroke="yellow" activeDot={{ r: 8 }} />
      </LineChart>
    </ResponsiveContainer>
  );
}

export default JockeyStatisticsGraph;